import React, {useState, useEffect} from 'react'
import { Link } from "react-router-dom";
import {get_all_posts, get_featured_post} from "../services/blog.services"

function Blog() {
    const [blogs, setBlogs] = useState([])
    const [featuredPosts, setFeaturedPosts] = useState([])

    useEffect(() => {
        get_all_posts().then(
            (res) => {
                setBlogs(res)
            }
        )
        get_featured_post().then(
            (res) => {
                setFeaturedPosts(res[0])
            }
        )

    }, [])

    const getBlogs = () => {
        let list = [];
        let result = [];

        blogs.map(blogPost => {
            return list.push(
                <div className="row g-0 border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
                    <div className="col p-4 d-flex flex-column position-static">
                        <h3 className="mb-0">{blogPost.title}</h3>
                        <div className="mb-1 text-muted">{blogPost.month} {blogPost.day} </div>
                        <p className="card-text mb-auto">{blogPost.excerpt}</p>
                        <Link to={`blog/${blogPost.slug}`} className="stretched-link" target="_blank" >Continue reading...</Link>
                    </div>
                    <div className="col-auto d-none d-lg-block">
                        <img 
                        width="200" height="210"
                        src={blogPost.thumbnail} alt="blog post" />
                    </div>
                </div>
            )
        });

        for (let i = 0; i < list.length; i +=2) {
            result.push(
                <div key={i} className="row mb-2">
                    <div className="col-md-6">
                        {list[i]}
                    </div>
                    <div className="col-md-6">
                        {list[i+1] ? list[i+1] : null}
                    </div>
                </div>
            )
        }

        return result;
    }
    return (
        <div >
            <main className="container">
                <div className="p-4 p-md-5 mb-4 mt-4 text-white bg-dark rounded">
                    <div className="col-md-6 px-0">
                        <h1 className="display-4 fst-italic">{featuredPosts.title}</h1>
                        <p className="lead my-3">{featuredPosts.excerpt}</p>
                        <p className="lead mb-0">
                            <Link to={`/blog/${featuredPosts.slug}`} className="text-white fw-bold" t="_blank">Continue reading...</Link>
                        </p>
                    </div>
                </div>
                <div className="row mb-2">
                    {getBlogs()}
                </div>
            </main>
        </div>
    )
}

export default Blog
