import axios from "axios";

export const get_all_posts = async () => {
    try{
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/blog/`);
        return res.data
    }catch(e){
        return e.message
    }
}

export const get_featured_post = async () => {
    try{
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/blog/featured/`);
        return res.data
    }catch(e){
        return e.message
    }
}

export const get_detailed_post = async (slug) => { 
    try{
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/blog/${slug}/`);
        return res.data
    }catch(e){
        return e.message
    }
}