import React from 'react'
import paul from "../img/laptop.jpg";
import styles from "../css/home.module.css";
import pro from  "../css/progress.module.css";
import { useForm } from 'react-hook-form';
import validator from "validator"
import myfile from "../img/Paul_Awolesi_Resume.pdf"
import {feedback} from "../services/home.services"
import prog from "../css/straight.module.css"


function Home() {
    const { register, errors, handleSubmit } = useForm();
    const blogMain = {
        backgroundColor : "#040402",
        color: "#6F6F6E"
    }
    const line = {
        border: "1px dashed white",
        "border-style": "dashed",
        color: "white",
        width: "97%",
    }
    const red = {
        color: "white"
    }
    const market = {'width':'90%','background-color':'#50A3FF'}
    const onSubmit = async (data) => {
        feedback(data).then(
            (res) => {
                if (res.status === 201) {
                    window.location.reload();
                    alert("Request Submitted.");
                }else {
                    alert("There was an error processing your request. \n Try again later.")
                }
            }
        )
    }
    return (
        <div style={blogMain}>
            <main className="container-fluid pb-5">
                <div className="container">
                    <div className="row p-4 p-md-5 mb-4 bg-transparent rounded">
                        <div className="col-md-6 ">
                            <div className={`p-5  ${styles.descDiv}`}>
                                <h1 className={styles.desc}>Hello Guys!</h1>
                                <h1 className={styles.desc}>I'm Paul A.</h1>
                                <p className={`text-wrap ${styles.desc_text}`}>
                                    I have a deep love for technology and "Automating the boring stuff". 
                                    How to make life easier for people with technology; How to build 
                                    enterprise solutions that help solve real life problems are just a 
                                    few of the things that inspire my love for technology.
                                </p>
                                <a type="button" href={myfile} download="Paul_Awolesi_Resume.pdf" 
                                    className={styles.descBtn}>
                                    <i className="fas fa-coffee"></i> Get Resume
                                </a>
                            </div>
                        </div>
                        <div className={`col-md-6 col-sm-12 position-relative ${styles.outer_img_div}`}>
                            <div className={`position-absolute top-50 start-50 translate-middle ${styles.inner_img_div}`}>
                                <img src={paul} alt="main thumbnail"
                                width={400} height={500}
                                className={`${styles.img1}`} />
                            </div>
                        </div>
                    </div>
                </div>
                <div className={`row justify-content-end  ${styles.line_dash}`}>
                    <div className="col-md-6 col-sm-12 position-relative ">
                        <span className={styles.vertical}>|</span>
                        <span><hr className="position-absolute top-50 end-0 translate-middle-y pl-2" style={line} /></span>
                    </div>
                </div>
                <div className="container p-4 p-md-5">
                    <div className="text-center">
                        <h1 className={`text-white display-3 pt-4 fw-bold ${styles.main_skill}`}>Main Skills</h1>
                        <p>
                            We see all types of projects as if they were ours. 
                            This brings us closer to our clients' projects <br className={` ${styles.br}`}/>
                            bringing much more confidence and commitment.
                        </p>
                    </div>
                    <div className={`row d-flex p-4 p-md-5 mb-5 justify-content-center ${styles.mt_100} `}>
                        <div className="row">
                            <div className="col-3">
                                <div className={`${pro.progress} ${pro.blue}`}> 
                                    <span className={pro.progress_left}> 
                                        <span className={pro.progress_bar}></span> 
                                    </span> 
                                    <span className={pro.progress_right}> 
                                        <span className={pro.progress_bar}></span> 
                                    </span>
                                    <div className={pro.progress_value}>98%</div>
                                </div>
                            </div>
                            <div className="col-3">
                                <div className={`${pro.progress} ${pro.two}`}> 
                                    <span className={pro.progress_left}> 
                                        <span className={pro.progress_bar}></span> 
                                    </span> 
                                    <span className={pro.progress_right}> 
                                        <span className={pro.progress_bar}></span> 
                                    </span>
                                    <div className={pro.progress_value}>85%</div>
                                </div>
                            </div>
                            <div className="col-3">
                                <div className={`${pro.progress} ${pro.three}`}> 
                                    <span className={pro.progress_left}> 
                                        <span className={pro.progress_bar}></span> 
                                    </span> 
                                    <span className={pro.progress_right}> 
                                        <span className={pro.progress_bar}></span> 
                                    </span>
                                    <div className={pro.progress_value}>90%</div>
                                </div>
                            </div>
                            <div className="col-3 ">
                                <div className={`${pro.progress} ${pro.four}`}> 
                                    <span className={pro.progress_left}> 
                                        <span className={pro.progress_bar}></span> 
                                    </span> 
                                    <span className={pro.progress_right}> 
                                        <span className={pro.progress_bar}></span> 
                                    </span>
                                    <div className={pro.progress_value}>95%</div>
                                </div>
                            </div>
                        </div>
                        <div className={`row text-white fw-bold fs-5 ${styles.skill_text}`}>
                            <div className="col-3  ">Marketing &amp; Branding</div>
                            <div className={`col-3 ${styles.software} `}>Software Development</div>
                            <div className="col-3  ">Web Development</div>
                            <div className="col-3 ">Wordpress Design</div>
                        </div>
                    </div>
                    <div className='col-lg-6 pt-4'>
                        <div className={`container ${styles.pbar}`}>
                            <div className='row pl-2'>
                                <div className='col-md-12'>
                                    <h3 className={prog.progress_title}>Marketing &amp; Branding</h3>
                                    <div className={prog.progress}>
                                        <div className={prog.progress_bar} style={market}>
                                            <div className={prog.progress_value}>90%</div>
                                        </div>
                                    </div>
                                    <h3 className={prog.progress_title}>Software Development</h3>
                                    <div className={prog.progress}>
                                        <div className={prog.progress_bar} style={{'background':'#F5FF2D','width':'80%'}}>
                                            <div className={prog.progress_value}>90%</div>
                                        </div>
                                    </div>
                                    <h3 className={prog.progress_title}>Web Development</h3>
                                    <div className={prog.progress}>
                                        <div className={prog.progress_bar} style={{'width':'94%', background:'#3E008E'}}>
                                            <div className={prog.progress_value}>94%</div>
                                        </div>
                                    </div>
                                    <h3 className={prog.progress_title}>Wordpress Design</h3>
                                    <div className={prog.progress}>
                                        <div className={prog.progress_bar} style={{'width':'80%', 'background':'#f2545b'}}>
                                            <div className={prog.progress_value}>80%</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={`row justify-content-start text-end ${styles.line_dash}`}>
                    <div className="col-md-6 position-relative">
                        <hr className="position-absolute top-50 end-0 translate-middle-y w-100 me-4" style={line} />
                        <span className={styles.vertical}>|</span>
                    </div>
                </div>
                <div className="container p-4 mt-2 mb-5">
                    <div>
                        <div className="p-2">
                            <p className={`${styles.hoz}`}>__</p>
                            <h2 className="fs-1 pb-3 text-white fw-bold">Our Services</h2>
                            <p className="py-2 pb-3 fw-bold">Focused on results we seek to raise the level of our customers.</p>
                        </div>
                        <div className="row pb-5">
                            <div className={`col-md-4 px-4 ${styles.col_4}`}>
                                <div className={`text-center  ${styles.service_col} ${styles.first}`}>
                                    <i className={`fas fa-globe ${styles.service_icon} `}></i>
                                    <h5 className="fw-bold text-white p-2">Website Pro</h5>
                                    <p>
                                        We build professional responsive websites 
                                        optimized for the most popular search engines.
                                    </p>
                                    <i className="far fa-arrow-alt-circle-right fs-4 fs-4"></i>
                                </div>
                            </div>
                            <div className={`col-md-4 px-4 ${styles.col_4}`}>
                                <div className={`text-center ${styles.service_col}`}>
                                    <i className={`fas fa-wrench ${styles.service_icon} `}></i>
                                    <h5 className="fw-bold text-white p-2">Web application</h5>
                                    <p>
                                        We build applications for different purposes 
                                        using technologies that allow you more security.
                                    </p>
                                    <i className="far fa-arrow-alt-circle-right fs-4"></i>
                                </div>
                            </div>
                            <div className={`col-md-4 px-4 ${styles.col_4}`}>
                                <div className={`text-center ${styles.service_col}`}>
                                    <i className={`	fas fa-cart-plus ${styles.service_icon} `}></i>
                                    <h5 className="fw-bold text-white p-2">E-commerce</h5>
                                    <p>
                                        Increase your sales with an incredible online store, 
                                        full of features and functionality.
                                    </p>
                                    <i className="far fa-arrow-alt-circle-right fs-4"></i>
                                </div>
                            </div>
                        </div>
                        <div className="row ">
                            <div className={`col-md-4 px-4 ${styles.col_4}`}>
                                <div className={`text-center ${styles.service_col}`}>
                                    <i className={`far fa-chart-bar ${styles.service_icon} `}></i>
                                    <h5 className="fw-bold text-white p-2">Digital Marketing</h5>
                                    <p>
                                        We work to promote your brand in partnership 
                                        with the best marketing platforms today
                                    </p>
                                    <i className="far fa-arrow-alt-circle-right fs-4"></i>
                                </div>
                            </div>
                            <div className={`col-md-4 px-4 ${styles.col_4}`}>
                                <div className={`text-center ${styles.service_col}`}>
                                    <i className={`far fa-lightbulb ${styles.service_icon} `}></i>
                                    <h5 className="fw-bold text-white p-2">Brand Creation</h5>
                                    <p>
                                        We create your brand thinking about your 
                                        target audience using design techniques.
                                    </p>
                                    <i className="far fa-arrow-alt-circle-right fs-4"></i>
                                </div>
                            </div>
                            <div className={`col-md-4 px-4 ${styles.col_4}`}>
                                <div className={`text-center ${styles.service_col} ${styles.last}`}>
                                    <i className={`fas fa-link ${styles.service_icon} `}></i>
                                    <h5 className="fw-bold text-white p-2">Software Development</h5>
                                    <p>
                                        We build professional software that meet 
                                        the needs of your company using the latest tech.
                                    </p>
                                    <i className="far fa-arrow-alt-circle-right fs-4"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="contact" className={`row justify-content-end ${styles.line_dash}`}>
                    <div className="col-md-6 position-relative">
                        <span className={styles.vertical}>|</span>
                        <span><hr className="position-absolute top-50 end-0 translate-middle-y pl-2" style={line} /></span>
                    </div>
                </div>
                <div className="container">
                    <div>
                        <div className={`row d-flex p-4 p-md-5 mb-5 justify-content-center`}>
                            <div className="col-md-6">
                                <div className="px-3 pt-0">
                                    <p className={`${styles.hoz}`}>__</p>
                                    <h2 className="fs-1 pb-3 text-white fw-bold">Let's Talk?</h2>
                                    <p className="py-2 pe-2 fw-bold">
                                        Don't wait until tomorrow. Talk to one of our consultants today and 
                                        learn how to start leveraging your business
                                    </p>
                                </div>
                            </div>
                            <div className="col-md-6" >
                                <div className="p-3 pt-5">
                                    <form onSubmit={handleSubmit(onSubmit)} className="p-3 ">
                                        <div className="mb-3">
                                            <input type="email" name="email" className="form-control text-white bg-transparent" 
                                            id="email"  placeholder="Email" ref={register({
                                                required: "This field is required",
                                                validate: {
                                                    emailVal : (value) => validator.isEmail
                                                },
                                                minLength: {
                                                    value: 8,
                                                    message: 'Minimum of 8 character length' 
                                                }
                                            })}   />
                                            <span style={red}> {errors.email?.message} </span>
                                            {errors.email?.type === "emailVal" && (
                                            <span style={red}>Enter a valid email address.</span>
                                            )}
                                        </div>
                                        <div className="mb-3">
                                            <input type="text" className="form-control text-white bg-transparent"  id="name" 
                                            placeholder="Name" ref={register({
                                                required: "This field is required",
                                                minLength: {
                                                    value: 4,
                                                    message: 'Minimum of 4 character length' 
                                                }
                                            })} name="name" />
                                            <span style={red}> {errors.name?.message} </span>
                                        </div>
                                        <div className="mb-3">
                                            <input type="text" className="form-control text-white bg-transparent"  id="phone" 
                                            placeholder="Phone" ref={register({
                                                required: "This field is required",
                                                minLength: {
                                                    value: 9,
                                                    message: 'Minimum of 9 character length' 
                                                }
                                            })} name="phone" />
                                            <span style={red}> {errors.phone?.message} </span>
                                        </div>
                                        <div className="mb-3">
                                            <textarea className="form-control text-white bg-transparent"  id="message" 
                                            name="message" placeholder="Leave a message" rows="3" 
                                            ref={register({
                                                required: "This field is required",
                                                minLength: {
                                                    value: 20,
                                                    message: 'Minimum of 20 character length' 
                                                }
                                            })}></textarea>
                                            <span style={red}> {errors.message?.message} </span>
                                        </div>
                                        <div className="mb-3">
                                            <button className={`rounded ${styles.btn}`} type="submit">
                                                Next <i className="far fa-arrow-alt-circle-right"></i>
                                            </button>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
            <script>
                
            </script>
        </div>
    )
}

export default Home;
