import React from 'react'
import {Link, NavLink} from "react-router-dom";
import styles from "../css/nav.module.css"

function Navbar() {
    const blogMain = {
        backgroundColor : "#040402",
    }
    return (
        <div>
            <nav className="navbar navbar-expand-lg navbar-dark sticky-top" style={blogMain}>
                <div className="container">
                    <NavLink className={`navbar-brand ${styles.navBrand}`}  to="/">Cypherspot</NavLink>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" 
                    data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" 
                    aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                        <div className="navbar-nav m-auto">
                            <NavLink className={styles.navi} aria-current="page" exact to="/"
                                >Home</NavLink>
                            <NavLink className={styles.navi} exact to="/blog"
                                >Blog</NavLink>
                            <NavLink className={styles.navi} exact to="/resume"
                                >Resume</NavLink>
                            <div className={`nav-item dropdown ${styles.drop}`} >
                                <Link className={`nav-link dropdown-toggle text-white ${styles.drop_main}`}
                                    id="navbarDark" role="button" 
                                    data-bs-toggle="dropdown" aria-expanded="false"
                                >
                                    Portfolio
                                </Link>
                                <div className="dropdown-menu dropdown-menu-dark z-index-1" 
                                    aria-labelledby="navbarDark">
                                    <a className="dropdown-item" href="#">Blog</a>
                                    <a className="dropdown-item" href="#">Url Shortener</a>
                                    <a className="dropdown-item" href="#">Todo App</a>
                                    <a className="dropdown-item" href="#">ML Algorithm</a>
                                    <a className="dropdown-item" href="#">Polling App</a>
                                    <a className="dropdown-item" href="#">Weather App</a>
                                    <a className="dropdown-item" href="#">School Page</a>
                                </div>
                            </div>
                            <a className={styles.navi} exact href="/#contact"
                                >Contact</a>
                        </div>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default Navbar
